@import '../Assets/scss/styles.scss';


.pageGalerie{
    padding:130px 0 100px 0;

    @include xl{
        padding:0;
    }



    .titreAnimationWrapper{
        left:31px;
        top:88px;

        height:auto;
        width:100px;
        position:absolute;  
        overflow: hidden;

        z-index: 1000;

        @include xl{
            left:51px;
        }
        
        // background:blue; 

        .titrePageGalerie{
            font-size: 25px;
            font-weight: 300;
            line-height: 1.6;
            letter-spacing: -0.6px;
    
            text-align: left;
            
            // right:0;
            // top:88px;
            position:absolute;
            transform:translateY(100%);
            transition:transform 650ms ease-out;
    
    
        }

        ul{
            padding: 0;
            list-style-type: none;
            margin-top: 40px;

            li{
                margin-bottom: 1px;

                &:hover{
                    color: grey;
                }

                &.active{
                    color: rgb(88, 88, 88);
                }
            }
        }
    }
}




.galerieMobile{
    padding-top:30px;
    // position:relative;

    iframe{
        width: 100vw;
    }

    .IMGMobileLigne{
        position:relative;
        // background:orange;

        .IMGMobile{
            position:absolute;

            overflow:hidden;
            // background:blue;
    
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                transform: translateX(-100%) scale(0.9);
                transition : all 280ms linear;
            }
        }

    }

}



.galeriePC::-webkit-scrollbar {
    display: none;
  }
  
  .galeriePCWrapper::-webkit-scrollbar {
    display: none;
  }

